.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-bg {
  animation: App-bg-spin infinite 0.3s linear;
}
.App-header {
  background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-start {
  background-color: #61dafb;
  font-size: larger;
  border: #61dafb;
}

@keyframes App-bg-spin {
  0% {
    filter: brightness(200%);
    width: 100vw;
    height: 100vh;
  }
  33%{
    width: 105vw;
    height: 105vh;    
  }
  66%{
    filter: none;
    width: 95vw;
    height: 95vh;   
  }
  100% {
    width: 100vw;
    height: 100vh;
  }
}
